@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
.body {
  touch-action: none;
  margin: auto;
  height: 100vmin;
  width: 100vmin;
}

.fill-parent {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-margin {
  width: 100%;
  height: 5%;
}
.drum-machine__container {
  background-color: #444;
  height: 90%;
  width: 90%;
  margin: auto;
  border-radius: 3%;
}

.display__container {
  width: 80%;
  height: 15%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.display {
  width: 90%;
  height: 50%;
  margin: auto;
  background-color: #131;
  color: #5a5;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.display-text {
  font-family: 'VT323', monospace;
  text-transform: uppercase;
  text-align: center;
  font-size: 5vmin;
  margin: 0;
}
.pads__container {
  height: 85%;
  width: 85%;
  margin: auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.single-pad__container {
  height: 30%;
  width: 30%;
}

.drum-pad {
  height: 90%;
  width: 90%;
  margin: auto;
  background-color: darkgrey;
  border-radius: 10%;
}
.drum-pad p {
  font-family: 'Black Ops One',sans-serif;
  font-size: 5vmin;
}
